/* Theme color for link/buttons:
rgba( 22, 230, 137, 1 )
OR
007bff */

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a {
  color: black;
  text-decoration: none;
}

/* a:hover {
  color: black;
} */

.link {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}

.link:hover {
  color: #0056b3;
  text-decoration: none;
}

.btn {
  background-color: #0099ff!important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.btn:hover {
  background-color: #2caaff!important;
}



