.backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100; /* very important */
    left: 0;
    top: 0; /* to position in top-left corner */
    background: rgba(0, 0, 0, 0.5); /* black w/ 50% transparency */
  }


